body {
  color: #1f2937;
  background-color: #f8fafc;
  font-family: Inter, sans-serif;
  line-height: 1.5;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

.navbar {
  z-index: 1000;
  background-color: #fff;
  padding: .75rem 0;
  position: sticky;
  top: 0;
  box-shadow: 0 1px 2px #0000000d;
}

.navbar-brand {
  color: #1f2937;
  align-items: center;
  font-weight: 600;
  text-decoration: none;
  display: flex;
}

.navbar-brand img {
  margin-right: .75rem;
}

.navbar-brand span {
  font-size: 1.125rem;
}

.navbar-nav {
  margin-left: auto;
}

.navbar-nav .nav-link {
  color: #1f2937;
  border-radius: .375rem;
  margin: 0 .25rem;
  padding: .5rem 1rem;
  font-weight: 500;
  transition: all .2s;
}

.navbar-nav .nav-link:hover {
  color: #4f46e5;
  background-color: #f8fafc;
}

.navbar-nav .nav-link.active {
  color: #4f46e5;
  background-color: #eef2ff;
  font-weight: 600;
}

.version-tag {
  color: #64748b;
  background: #f1f5f9;
  border-radius: 9999px;
  margin-left: .5rem;
  padding: .25rem .5rem;
  font-size: .75rem;
  font-weight: 500;
  display: inline-block;
}

.hero {
  background-color: #fff;
  border-bottom: 1px solid #e5e7eb;
  padding: 4rem 0;
}

.hero h1 {
  color: #111827;
  margin-bottom: 1rem;
  font-size: 2.5rem;
  font-weight: 700;
}

.hero p {
  color: #4b5563;
  margin-bottom: 2rem;
  font-size: 1.125rem;
}

.btn {
  border-radius: .375rem;
  padding: .5rem 1rem;
  font-weight: 500;
  transition: all .2s;
}

.btn-primary {
  color: #fff;
  background-color: #4f46e5;
  border-color: #4f46e5;
}

.btn-primary:hover {
  background-color: #4338ca;
  border-color: #4338ca;
}

.text-primary {
  color: #4f46e5 !important;
}

.bg-primary {
  background-color: #4f46e5 !important;
}

.bg-light {
  background-color: #f8fafc !important;
}
